<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>商品类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.goodsId">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <span class="demonstration">创建时间</span>
            <el-date-picker
                    v-model="dateTime"
                    type="daterange"
                    placeholder="选择时间范围"
                    range-separator="/"
                    @change="conditionChange">
            </el-date-picker>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page.pageNum"
                               :page-sizes="[15,30,45,60]" :page-size="currencyTrans.page.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import {characterSize} from '@/utils/validate';
    import {
        goodsquery,
        getexpress,
        reviewgoods,
        lockgoods,
        cancelgoods,
        issuegoods,
        postexpress,
        getgoodslist
    } from '@/api/awaits';
    //启用禁用筛选条件
    const allCoin = {baseSeq: '', chineseName: '全部商品', englishName: 'All'};
    const express = {id: 0, expressName: '请选择快递机构'};
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 1, display_name: '等待审核'},
        {key: 2, display_name: '等待收货'},
        {key: 3, display_name: '成功提货'},
        {key: 4, display_name: '系统取消'},
        {key: 5, display_name: '用户取消'},
        {key: 6, display_name: '正在处理'},
    ];

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '' || value === 0) {
                    callback(new Error('请选择快递'));
                }
                callback();
            };
            let validateNo = (rule, value, callback) => {
                if (characterSize(value) > 40) {
                    callback(new Error('最大40字符'));
                } else if (value === '') {
                    callback(new Error('请输入信息'));
                }
                callback();
            };
            return {
                //搜索需求数据等
                express,
                allCoin,
                dateTime: '',
                coinList: [],
                goodsall: {
                    types: [2]
                },
                goodsList: [],
                dateTime: '',
                tableData: [],
                deliverylist: [],
                xpressVisible: false,
                xpress: {
                    expressName: '',
                },
                applygoods: {
                    id: '',
                    expressAgentId: 0,
                    expressNo: ''
                },
                formLabelWidth: '120px',
                total: null,
                dialogFormVisible: false,
                lockWithdrawalVo: {
                    id: '',
                    status: ''
                },

                temp: {
                    mobile: '',
                    location: '',
                    realName: '',
                    deliveryAddress: '',
                    applyDeliveryCount: '',
                    createTime: '',
                    goodsName: '',

                },
                currencyTrans: {
                    username: '',
                    goodsId: '',
                    status: '',
                    startDate: '',
                    endDate: '',
                    page: {
                        pageNum: 1,
                        pageSize: 15,

                    }

                },
                msg: '',
                listLoading: true,
                appgoods: {
                    expressAgentId: [{validator: validate, trigger: 'blur'}],
                    expressNo: [{validator: validateNo, trigger: 'blur'}],
                },
                xpressgoods: {
                    expressName: [{validator: validateNo, trigger: 'blur'}],
                },
                tableHeader: [
                    {
                        label: "单号id",
                        prop: "id",
                        align: 'center',
                        width: 50
                    },
                    {
                        label: "会员UID",
                        prop: "userUid",
                        align: 'center',
                        width: 70
                    }, {
                        label: "登录名",
                        prop: "loginName",
                        align: 'center',
                        width: 90
                    }, {
                        label: "邮箱地址",
                        prop: "email",
                        align: 'center',
                        width: 150
                    }, {
                        label: "真实姓名",
                        prop: "realName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "商品类型",
                        prop: "goodsName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "状态",
                        prop: "status",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "数量",
                        prop: "applyDeliveryCount",
                        width: 45,
                        align: 'center'
                    }, {
                        label: "提货人",
                        prop: "realName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "手机号",
                        prop: "mobile",
                        width: 130,
                        align: 'center'
                    }, {
                        label: "所在区域",
                        prop: "location",
                        width: 120
                    }, {
                        label: "收货地址",
                        prop: "deliveryAddress",
                        width: 350
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后修改时间",
                        prop: "updateTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {


            //获取当前页数
            userPage(val) {
                this.currencyTrans.page.pageNum = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.page.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                console.log(this.dateTime)
                if (this.dateTime[0] === undefined || this.dateTime[0] === null) {
                    this.currencyTrans.startDate = '';
                    this.currencyTrans.endDate = '';
                    this.getList();
                } else {
                    this.getList();
                }
            },
            conditionChange(val) {
                console.log(val);
                let times = val.split('/');
                this.currencyTrans.startDate = times[0];
                this.currencyTrans.endDate = times[1];
            },
            /**
             * 移入问题列表方法
             * @param id
             * @param status
             */
            issuegoods(id, type) {
                issuegoods(id, type).then(response => {
                    if (response.result) {
                        this.msg = "已移入问题列表";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = "移入问题列表失败";
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 锁定订单方法
             * @param id
             * @param status
             */
            lockgoods(id, status, msg) {
                lockgoods(id, status).then(response => {
                    if (response.result) {
                        this.msg = '已' + msg + '此单';
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = msg + '操作失败';
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 取消提现方法
             * @param id
             */
            cancelgoods(id) {
                cancelgoods(id).then(response => {
                    if (response.result) {
                        this.msg = "已取消提现";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 审核填写信息
             * @param vol
             */
            passApproval(vol) {
                getexpress().then(response => {
                    this.deliverylist = response.data;
                    this.deliverylist.unshift(this.express);
                });
                this.temp = vol;
                this.applygoods.id = vol.id;
                this.dialogFormVisible = true;
            }
            ,
            /**
             * 审核通过方法
             * @param vol
             */
            reviewgoods() {
                let _this = this;
                this.$refs['applygoods'].validate((valid) => {
                    if (valid) {
                        console.log(this.applygoods);
                        reviewgoods(this.applygoods).then(response => {
                            if (response.result) {
                                this.msg = "已提交";
                                this.$message({
                                    type: 'success',
                                    message: this.msg
                                });
                                this.dialogFormVisible = false;
                            } else {
                                this.msg = response.msg;
                                this.$message({
                                    type: 'error',
                                    message: this.msg
                                });

                            }
                            this.getList();
                        });
                    }
                });
            },
            /**
             * 请求列表方法
             */
            getList() {
                this.listLoading = true;
                goodsquery(this.currencyTrans).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            },
            /**
             * 添加快递信息
             */
            addpostexpress() {
                this.xpress.expressName = '',
                    this.xpressVisible = true;
            },
            postexpress() {
                this.$refs['xpress'].validate((valid) => {
                    if (valid) {
                        postexpress(this.xpress).then(response => {
                            if (response.result) {
                                this.xpressVisible = false;
                                this.msg = "已添加";
                                this.$message({
                                    type: 'success',
                                    message: this.msg
                                });
                            } else {
                                this.msg = response.msg;
                                this.$message({
                                    type: 'error',
                                    message: this.msg
                                });

                            }
                            this.getList();
                        });
                    }
                });
            },
            getgoods() {
                getgoodslist(this.goodsall).then(response => {
                    this.coinList = response.data;
                    this.coinList.unshift(this.allCoin);
                })
            }
        },
        created() {
            this.getList();
            this.getgoods();
        }
    }
</script>



